

















import AccountImage from '../components/AccountImage'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default{
components: {
AccountImage,
XHeader,
Group,
XButton,
XInput,
Toast
},
data () {
return {
account: '',
isAccount: false,
error: '',
show: false,
loading: false,
password: '',
confirm: '',
accountSuccess: false
}
},
methods: {

accountOnBlur () {
this.validateAccount()
},
validateAccount () {
let account = this.account
var length = account.length
if (!account) {
this.error = '账户名不可为空'
this.show = true
return false
} else if (length < 1) {
this.error = '账户名至少3位'
this.show = true
} else if (length > 13) {
this.error = '账户名过长'
this.show = true
return false
} else if (!/^[.12345a-z]+$/.test(this.account)) {
this.error = '账户名只能是字母、数字的组合'
this.show = true
return false
} else {
let account = this.account
this.$http.get(`/chain/accounts/eos/${account}`).then(res => {
let data = res.data
console.log(res, data)
if (data.code === 400) {
this.show = false
this.isAccount = true
return true
} else {
this.error = '账户已存在'
this.show = true
return false
}
}).catch(ex => {
this.error = '请求错误，请稍后再试'
this.show = true
return false
})
}
},
confirmOnBlur () {
if (this.password === '') {
this.error = '请输入密码'
this.show = true
return false
} else if (this.password !== this.confirm) {
this.error = '两次密码输入不一致！'
this.show = true
return false
} else {
return true
}
},
submit () {
let _this = this
this.loading = true
if (this.isAccount) {
this.accountSuccess = true
let getData = this.$common.create_account(this.account, this.password)
this.$http.post('/chain/accounts/faucet', getData.params).then(res => {
let data = res.data
if (data.code === 200) {
console.log(this.$common.get_wallets())
let wallets = this.$common.get_wallets()
wallets.push(getData.wallet)
this.$common.set_wallets(wallets)
this.$router.push({path: '/wallet-create-success', query: {account: this.account}})
} else {
_this.error = '创建账户失败，请重试'
_this.show = true
}
})
}
}
}
}



