
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Drawer from 'vux/src/components/drawer/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { mapState, mapActions } from 'vuex'
import util from '../libs/utils'
import AccountImage from '../components/AccountImage'
export default {
name: 'index',
directives: {
TransferDom
},
components: {
Radio,
Group,
Cell,
Badge,
Drawer,
ButtonTab,
ButtonTabItem,
ViewBox,
XHeader,
Tabbar,
TabbarItem,
Loading,
Actionsheet,
AccountImage
},
methods: {
onPlacementChange (val) {
/** hide drawer before changing position **/
this.drawerVisibility = false
setTimeout(one => {
this.showPlacementValue = val
}, 400)
},
onClickMore () {
this.showMenu = true
},
changeLocale (locale) {
util.setStore('_locale', locale)
this.$i18n.locale = locale
},
...mapActions([
'updateDemoPosition'
]),
qrcode () {


























}
},
mounted () {
this.handler = () => {
if (this.path === '/') {
this.box = document.querySelector('#demo_list_box')
this.updateDemoPosition(this.box.scrollTop)
}
}
},
beforeDestroy () {
this.box && this.box.removeEventListener('scroll', this.handler, false)
},
watch: {
},
computed: {
...mapState({
route: state => state.route,
deviceready: state => state.app.deviceready,
demoTop: state => state.vux.demoScrollTop
}),
isShowBar () {
if (/component/.test(this.path)) {
return true
}
return false
},
leftOptions () {
return {
showBack: this.path !== '/'
}
},
rightOptions () {
return {
showMore: true
}
},
headerTransition () {
if (!this.direction) return ''
return this.direction === 'forward' ? 'vux-header-fade-in-right' : 'vux-header-fade-in-left'
},
componentName () {
if (this.path) {
const parts = this.path.split('/')
if (/component/.test(this.path) && parts[2]) return parts[2]
}
},
isDemo () {
return /component|demo/.test(this.path)
},
isTabbarDemo () {
return /tabbar/.test(this.path)
},
viewTransition () {
if (!this.direction) return ''
return 'vux-pop-' + (this.direction === 'forward' ? 'in' : 'out')
}
},
data () {
return {
showMenu: false,
menus: {
'language.noop': '<span class="menu-title">Language</span>',
'zh-CN': '中文',
'en-US': 'English'
},
path: '/',
isLoading: false,
direction: 'forward',
drawerVisibility: false,
showMode: 'push',
showModeValue: 'push',
showPlacement: 'left',
showPlacementValue: 'left'
}
}
}



